<template>
  <div class="proxyin">
    <!-- 查询 -->
    <el-card class="main_proxyin">
      <el-form :inline="true" :model="queryfrom" class="me_from demo-form-inline">
        <el-form-item>
          <el-select style="width:190px;" v-model="queryfrom.projectId" placeholder="请选择项目">
             <el-option label="全部" value=""></el-option>
            <el-option
              v-for="(item,index) in $store.getters.projectAll"
              :key="item.projectId+index+'s'+item.projectName"
              :label="item.projectName"
              :value="item.projectId"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select style="width:190px;" v-model="queryfrom.status" placeholder="请选择状态">
            <el-option label="全部" :value="0"></el-option>
            <el-option label="已保存" :value="1"></el-option>
            <el-option label="已提交" :value="2"></el-option>
            <el-option label="已审核" :value="3"></el-option>
            <el-option label="已开票" :value="5"></el-option>
            <el-option label="已收佣" :value="6"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" style="background:#0b8d7e;border:none;" @click="Search">搜索</el-button>
          <el-button type="primary" style="background:#f1420ef7;border:none;" @click="AddPic">添加开票</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 查询 -->
    <!-- 表格数据 -->
   <el-card style="margin-top:20px;">
      <header style="font-weight: 800;padding-bottom: 8px;color: #000;">代理收佣列表</header>
      <el-table
        ref="table"
        height="410"
        :cell-style="cellStyle"
        :header-cell-style="{background:'#edfaf8',color:'#333333'}"
        :row-class-name="tableRowClassName"
        :data="tableData"
        border
        style="width: 100%"
      >
        <el-table-column prop="projectName" label="楼盘" width="100"></el-table-column>
        <el-table-column prop="time"  width="180" label="日期"></el-table-column>
        <el-table-column prop="standName" label="对佣人"></el-table-column>
        <el-table-column prop="money"  label="开票金额"></el-table-column>
        <el-table-column prop="status" label="状态" width="130">
          <template
            slot-scope="scope"
          >{{scope.row.status===1?'已保存':scope.row.status===2?'已提交':scope.row.status===5?'已开票':scope.row.status===6?'已收佣':'已审核'}}</template>
        </el-table-column>
        <!-- <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">详情</el-button>
          </template>
        </el-table-column> -->
      </el-table>
      <Pagination
        @handleSizeChange="handleSizeChange"
        @handleCurrentChange="handleCurrentChange"
        :paginationData="paginationData"
      />
    </el-card>
    <!-- 表格数据 -->

    <!-- 添加发票 -->
    <addPro :dialogVisible="addProData"/>
    <!-- 添加发票 -->
  </div>
</template>
<script>
import myMixin from '@/mixin/index'
import {dateTime} from '@/utils/common'
import {agencyInvoiceList} from '@/api/business/proxyin'
import addPro from '@/components/business/commission/addPro'
export default {
     components:{
          addPro
     },
     mixins:[myMixin],
     data(){
          return{
               queryfrom:{
                    status:'',
                    projectId:'',
                    pageNo:1,
                    pageSize:15
               },
               tableData:[],
               paginationData:{
                  pageSize:15,
                  currentPage:1,
                  total:100
               },
               addProData:{//添加开票
                   trunoff:false,
                   isAgency:1
               }
          }
     },
     methods:{
        handleSizeChange(val){},
        handleCurrentChange(val){},
        //搜索
        Search(){
          this.requestList()
        },
        //添加
        AddPic(){
          this.addProData.trunoff=true
        },
        //数据
        requestList(){
             agencyInvoiceList(this.queryfrom).then(res=>{
             console.log(res)
             if(res.data.code==='000'){
                  this.tableData=res.data.bizData.items.map(item=>{
                       item.time=dateTime(item.time)
                       return item
                  })
             }
          })
        }
     },
     mounted(){
         this.requestList()
     }
};
</script>
<style lang="scss" scoped>
.proxyin .main_proxyin /deep/.el-card__body {
  padding-bottom: 0;
}
</style>